
import { defineComponent } from "vue";

import { modelWrapper, emptyDefinition } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";
import Example from "@/components/Example.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
        Example,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    methods: {
        blur(index: number) {
            if (this.model && this.model.length > index) {
                let definition = this.model[index].definition;

                if (definition && definition.endsWith(".")) {
                    this.model[index].definition = definition.replace(
                        /\.+$/,
                        ""
                    );
                }
            }
        },
    },
    data() {
        return {
            empty: emptyDefinition(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
