
import { defineComponent } from "vue";

import { modelWrapper, emptyExample } from "@/common";

import AddRemove from "@/components/AddRemove.vue";
import MultiLine from "@/components/MultiLine.vue";

export default defineComponent({
    components: {
        AddRemove,
        MultiLine,
    },
    props: {
        number: String,
        modelValue: Array,
    },
    methods: {
        blur(index: number) {
            if (this.model && this.model.length > index) {
                let example = this.model[index].example;

                if (
                    example &&
                    (example.startsWith('"') || example.endsWith('"'))
                ) {
                    this.model[index].example = example.replace(/^"+|"+$/g, "");
                }
            }
        },
    },
    data() {
        return {
            empty: emptyExample(),
        };
    },
    setup(props, { emit }) {
        return {
            model: modelWrapper(props, emit),
        };
    },
});
